
.wrap{
  position:relative;
  width:100%;
  max-width:1920px;
  margin:0 auto;
  font-family: 'IBM Plex Sans KR', sans-serif;
}
/* Header */
header{
  position:fixed;
  top:0;
  left:0;
  right:0;
  z-index:9999;
  border-bottom:1px solid #212223;
  box-sizing:border-box;
}
header .bg-body-tertiary{
  background-color:#0A0B0C !important;
}
header .navbar{
  padding-top:0;
  padding-bottom:0;
}
header .nav-link{
  color:#fff;
}
header .nav-link:hover, header .nav-link:active{
  color:#60A7AF;
}
header .dropdown button{
  background-color:#131518 !important;
  border:1px solid #363636;
  font-size:14px;
}
header .dropdown button:hover{
  background-color:#077D7D !important;
}
header .dropdown button:focus{
  background-color:#077D7D !important;
}
/* Container */
.main_container{
  padding:40px 0 0 0;
  display:flex;
  position:relative;
}
.container{
  padding:0;
}
.container_center{
  margin:0 auto;
  padding:0 20px;
  width:67%;
  max-width:1280px;
}
.container_left{
  position:fixed;
  left:0;
  width:16.5%;
  height:100vh;
  background-color:#131518;
}
.container_left ul li{
  padding:20px;
  color:#fff;
  font-weight:bold;
  border-bottom:1px solid #212223;
  box-sizing:border-box;
}
.container_left ul li:hover{
  background-color:#077D7D;
}
.container_left ul li a{
  display:block;
}
.container_right{
  padding:20px;
  position:fixed;
  right:0;
  width:16.5%;
  height:100vh;
  background-color:#131518;
}
.container_right .dropdown button{
  background-color:#077D7D;
  border:1px solid #003333;
  width:100%;
  height:75px;
}
.container_right .profile_dropdown .show{
  width:100%;
}
.modal-dialog{
  margin-top:100px;
}
.modalProfile{
  padding:10px;
}
.modalProfile strong{
  display:block;
}
.modalProfile span{
  flex-grow:1;
  font-size:12px;
  padding:2px 6px;
  border:1px solid #c0c0c0;
}

/* Page 홈 */
.home h1{
  text-align:center;
  font-size:48px;
  color:#E2AF75;
  font-weight:bold;
}
.btn_group1{
  display:flex;
  flex-direction: row;
  justify-content:center;
}
.btn_group1 button{
  margin:0 10px;
  padding:20px;
  height:75px;
  outline:none;
  border:none;
  background:#F7E1BB;
  border:1px solid #E2AF75;
  border-radius:8px;

}
.btn_group1 button:hover{
  background:#E2AF75;
  border:1px solid #F7E1BB;
  color:#fff;
}
.home_main{
  position:sticky;
  top:0;
  padding:100px 0;
  width:100%;
  background-color:#fff;
  z-index:3;
  text-align:center;
}
.home_profile{
  position:relative;
  padding:4rem 20px;
  text-align:center;
  background-color:#E3E3E3;
  z-index:10;
}
.home_profile h1{
  color:#003333
}
.home_profile > div{
  padding:100px 0;
}
.home_skill .skill_nav .nav-tabs, .home_skill .skill_nav .nav-tabs .nav-link{
  border-radius:0 !important;
  background-color:#D6D5D5;
}
.home_skill .nav-link{
  color:#0A0B0C;
}
.home_skill .nav-tabs .nav-link.active{
  background-color: #003333;
  border:1px solid #077D7D;
  color:#fff;
}
.home_skill .skill_nav > div{
  text-align:left;
  background-color:#f0f0f0;
  border:1px solid #D6D5D5;
}
.home_skill .skill_nav > div:last-child{
  padding:10px;
}
.home_slide .swiper .swiper-wrapper{
  padding:65px 0 0 0;
}
.home_slide .swiper-slide{
  display:flex;
  flex-direction:column;
  justify-content:space-between;
  padding:20px;
  height:250px;
  text-align:left;
  background-color:#D6D5D5;
  border-radius:10px;
}
.home_slide .swiper-slide:first-child {
  justify-content:flex-start;
  width:30%;
}
.home_slide .swiper-slide:nth-child(2n) {
  width:50%;
}
.home_slide .swiper-slide:nth-child(3n) {
  width:50%;
}
.home_slide .swiper-slide h1{
  text-align:left;
}
.home_slide .swiper-button-prev, .swiper-button-next{
  width:50px !important;
  height:50px !important;
  top:25px !important;
  border:1px solid #F7E1BB;
  background-color:#E2AF75;
  border-radius:10px;
}
.home_slide .swiper-button-prev{
  left: auto !important;
  right:70px !important;
}
.home_slide .swiper-button-prev:after, .home_slide .swiper-button-next:after{
  font-size:16px;
  color:#fff;
}
.home_slide .swiper-slide .btn_group1{
  justify-content:flex-end
}
/* Page 쇼핑몰 */
.contain_top{
  display:flex;
  justify-content:space-between;
  align-items:center;
}
.contain_top .input-group{
  width:300px;
}
.shop_title{
  padding:20px 0;
}
.cart_number{
  position:relative;
}
.cart_number > button {
  background-color:#E2AF75;
  border:1px solid #F7E1BB
}
.cart_number > button > span{
  padding-left:5px;
}
.cart_number .cart_toggle{
  position:absolute;
  right:0;
  min-width:300px;
  z-index:8888;
}

.cart_number .cart_toggle .cart_select_area .select_item{
  padding:10px 0;
  display:flex;
  justify-content:space-between;
  align-items:center;
}
.delete_item{
  border:0;
  outline:none;
}
.cart_number .cart_toggle .cart_total_area{
  padding:10px 0;
  display:flex;
  justify-content:space-between;
  border-top:1px solid #566A93;
}
.cart_number .cart_toggle .cart_button_area{
  display:flex;
  flex-direction:column;
}
.cart_number .cart_toggle .cart_button_area button{
  margin-bottom:10px;
  height:75px;
  color:#fff;
  background-color:#E2AF75;
  border:1px solid #F7E1BB;
  border-radius:6px;
}
.cart_number .cart_toggle .cart_button_area button:last-child{
  margin-bottom:0;
}
.mall_list .item .item_card_cart{
  position:relative;
}
.mall_list .item .item_card_cart .card_cartBtn{
  position:absolute;
  bottom:10px;
  right:10px;
  width:50px;
  height:50px;
  border-radius:8px;
  outline:none;
  border:0;
  background-color:#E2AF75;
  color:#fff;
}
.mall_list .item p{
  margin:10px 0;
}
.mall_list .item span.item_tag{
  padding:5px 10px;
  margin-right:20px;
  background-color:#F7E1BB;
  border:1px solid #E2AF75;
  border-radius:12px;
}
.mall_detail{
  display:flex;
  align-items:flex-end;
}
.mall_detail > div{
  flex:1 1 0;
}
.thumb_info{
  margin-right:20px;
  padding:20px;
  display:flex;
  width:771px;
  background-color:#E3E3E3;
  border-radius:16px;
  border:1px solid #D6D5D5
}
.thumb_info .thumb_img{
  flex:1;
}
.thumb_info .thumb_txt{
  flex:1;
  display:flex;
  flex-direction:column;
  justify-content:space-between;
  padding:10px;
}
.thumb_info .thumb_txt .nav-tabs {
  flex-direction:column;
  width:100%;
}
.thumb_info .nav-fill .nav-item .nav-link, .thumb_info .nav-justified .nav-item .nav-link{
  color:#0A0B0C;
  font-weight:bold;
}
.thumb_info .thumb_txt .nav-tabs .nav-item{
  border-bottom:1px solid #c0c0c0;
}
.thumb_info .thumb_txt .nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active{
  background:none;
  border-bottom:1px solid #E2AF75;
}
.thumb_info .thumb_txt .payment_option{
  width:100%;
}
.thumb_info .thumb_txt .payment_option .payment_button{
  display:flex;
  justify-content:flex-end;
}
.thumb_summary{
  flex:1 0 auto;
  padding:20px;
  background-color:#E3E3E3;
  border-radius:16px;
  border:1px solid #D6D5D5
}
.thumb_summary ul li{
  display:flex;
  align-items:center;
  flex-wrap:nowrap;
}
.thumb_summary ul li span{
  width:20%;
}
.thumb_summary  .summary_like{
  margin-top:20px;
  display:flex;
  justify-content:flex-end;
}
.thumb_summary  .summary_like button:first-child{
  margin-right:10px;
}
.product_3d{
  margin:20px 0;
  padding:20px 0;
  border-top:1px solid #c0c0c0;
  height:500px;
}
.product_info{
  padding:20px 0;
}
.mall_detail button{
  padding:5px 10px;
  outline:none;
  border:0;
  background-color:#fff;
  border:1px solid #E2AF75;
  border-radius:6px;
}
.mall_detail button:hover{
  background-color:#E2AF75;
  border:1px solid #F7E1BB;
  color:#fff;
}
.product_pay .btn_group1 button{
  background-color:#fff;
  border:1px solid #E2AF75;
  border-radius:6px;
  color:#0A0B0C
}
.product_pay .btn_group1 button:hover{
  background-color:#E2AF75;
  border:1px solid #F7E1BB;
  color:#fff;
}
.contain_title h1{
  padding:20px 0;
  font-size:22px
}
.cart_list .cart_myselect{
  width: 100%;
  table-layout: fixed;
  word-break:break-all;
  height:auto;
}
.cart_list .cart_myselect tbody tr th, .cart_list .cart_myselect tbody tr td{
  vertical-align:middle;
}
.cart_list .cart_myselect .inner_thum{
  width:40%;
  border-radius:12px;
  overflow:hidden;
}
.cart_list .cart_price, .cart_list  .cart_deliver, .cart_list  .cart_total {
  display:flex;
  justify-content:space-between;
}
/* page 포트폴리오 */
.myPortfolio .portfolio_list{
  padding-bottom:20px;
  border-bottom:1px solid #eeeeee;
  margin-bottom:20px;
}
.myPortfolio .portfolio_list .mySwiper{
  width:100%;
  text-align:center;
}
.myPortfolio .portfolio_list .mySwiper img{
  width:80%;
  border:1px solid #eeeeee;
}
.porfolModalWrap{
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  position:fixed;
  left:0;
  top:0;
  right:0;
  bottom:0;
  width:100vw;
  height:100vh;
  background-color:rgba(0,0,0,0.8);
  z-index:9999;
}
.porfol_detail{
  padding:20px;
  width:50%;
  background-color:#fff;
  overflow-y:scroll;
}
.porfol_detail .pd_text p, .porfol_detail .pd_text h2{
  margin-bottom:10px;
}
.porfol_detail > div{
  margin-bottom:20px;

}
.porfol_detail .pd_text > p:first-child{
  display:flex;
  justify-content:space-between;
  align-items:center;
}
.porfol_detail .pd_text > p:first-child button{
  padding:5px 10px;
  outline:none;
  border:1px solid #077D7D;
  background-color:#003333;
  color:#fff;
  border-radius:8px;
}
.porfol_detail .pd_text > p:first-child button:hover{
  border:1px solid #077D7D;
  background-color:#077D7D;
}
.porfol_detail .pd_tag{
  display:flex;
  flex-wrap:wrap;
}
.porfol_detail .pd_tag span{
  margin-right:10px;
  margin-bottom:10px;
  padding:10px 20px;
  background-color:#ececec;
  border-radius:10px;

}
.portfolio_gallery{
  display:flex;
  gap:20px;
}
.portfolio_gallery > div{
  padding:20px;
  cursor: pointer;
  border:1px solid #eeeeee;
  border-radius:16px;
}
.portfolio_gallery span{
  padding:4px;
  background-color:#eeeeee;
  border-radius:4px;
  margin-left:5px;
}
/* page 커뮤니티 */
.community_form .form_wrap form{
  display:flex;
  flex-direction:column;
}
.community_form .form_wrap label{
  margin-bottom:20px;
  display:block;
  width:100%;
}
.community_form .form_wrap input:first-child{
  display:block;
  padding:10px;
  margin-bottom:20px;
  width:100%;
  border:1px solid #c0c0c0;
  border-radius:12px;
  outline:none;
}
.community_form .form_wrap input:last-child{
  display:block;
  padding:10px;
  margin-top:20px;
  border:1px solid #c0c0c0;
  border-radius:12px;
  outline:none;
}
.community_list tbody tr{
  cursor: pointer;
}
.community_list tbody tr .board_like{
  margin-right:20px;
  outline:none;
  border:1px solid #363636;
  border-radius:6px;
  padding:4px;
}
/* page 데이터 */

.data_area .data_list{
  padding:20px;
  margin-bottom:20px;
  border:1px solid #eeeeee;
  cursor: pointer;
}
.data_area .data_list:hover{
  padding:20px;
  margin-bottom:20px;
  border:1px solid #003333;
  background-color:#077D7D;
  color:#fff;
}
.data_content{
  padding-top:20px;
  border-top:1px solid #c0c0c0;
}
.data_content button{
  outline:none;
  border:1px solid #363636;
  border-radius:6px;
  padding:4px;
}
.data_content button:hover{
  outline:none;
  border:1px solid #077D7D;
  border-radius:6px;
  padding:4px;
}
/* page 유기견 */
.dog_option{
  display:flex;
  justify-content:space-between;
  padding-bottom:20px;
}
.dog_option .dog_select, .dog_option button{
  flex-basis:32%;
}
.dog_result{
  padding:20px;
  display:flex;
  flex-direction:row;
  flex-wrap:wrap;
  gap:10px;
  background-color:#f8f8f8;
}
.dog_result .dog_card{
  padding:20px;
  width:32.5%;
  border:1px solid #60A7AF;
  border-radius:16px;
  background-color:#ffffff;
}
.dog_result .dog_card p{
  font-size:14px;
}
.dog_result .dog_card .dog_status{
  padding:4px;
  background-color:#60A7AF;
  color:#fff;
  font-weight:bold;
  text-align:center;
}
.dog_result .dog_card img{
  width:50%;
}
/* page 투두리스트 */
.todoform{
  display:flex;
  flex-direction:column;
}
.todoform label{
  margin-bottom:10px;
  display:flex;
  align-items:center;
  justify-content:space-between;
  flex-basis:20%;
}
.todoform label input, .todoform label select{
  flex-basis:80%;
  padding:10px;
  outline:none;
  border:1px solid #c0c0c0;
  border-radius:8px;
}
.todoform span{
  margin-bottom:20px;
  font-size:14px;
  color:#696969;
}
.TodoSignBtn{
  outline:none;
  border:1px solid #077D7D;
  border-radius:6px;
  padding:10px;
}
.TodoSignBtn:hover{
  border:1px solid #077D7D;
  background-color:#077D7D;
  color:#fff;
}
.todoForm .todoPlus{
  padding:20px;
  margin-bottom:20px;
  display:flex;
  justify-content:space-between;
  border-bottom:1px solid #077D7D;
}
.todoForm .todoPlus input{
  padding:5px;
  width:100%;
  outline:none;
  border:0;
}
.todoForm .todoPlus span{
  display:inline-block;
  width:30px;
  height:30px;
  background-color:#c0c0c0;
  border-radius:100%;
  text-align:center;
  color:#fff;
  font-size:18px;
  cursor: pointer;
}
.todoForm .todoPlus span:hover{
  background-color:#077D7D;
}
.todoForm .todoList{
  padding:20px;
  border:1px solid #077D7D;
  border-radius:20px;
}
.todoForm .todoList .todoListItem{
  display:flex;
  align-items:center;
  justify-content:space-between;
  padding:10px;
  border-bottom:1px solid #c0c0c0;
  font-size:18px;
}
.todoForm .todoList .todoListItem .todoCheck{
  font-size:16px;
  margin-right:20px;
  flex-grow:0;
}
.todoForm .todoList .todoListItem p{
  margin:0;
  flex-grow:2;
}
.todoForm .todoList .todoListItem span{
  display:inline-block;
  width:30px;
  height:30px;
  border:1px solid #c0c0c0;
  border-radius:100%;
  text-align:center;
  cursor: pointer;
}
.todoForm .todoList .todoListItem span:hover{
  border:1px solid #077D7D;
}
.todoForm .underline{
  text-decoration:line-through;
}
/* page 게임 */
.game_tab{
  display:flex;
  gap:20px;
}
.game_tab .game_list{
  width:30%;
  padding:20px;
  border:1px solid #077D7D;
  border-radius:200px;
  cursor:pointer;
}
.game_tab .game_list:hover{
  background-color:#077D7D;
  color:#fff;
}
.game_content{
  padding-top:20px;
  margin-top:20px;
  border-top:1px solid #c0c0c0;
}
/* page 게임 틱택톡 */
.tictactoc{
  text-align:center;
  display:flex;
  flex-direction:column;
  align-items:center;
  padding:50px;
}
.tictactoc p, .tictactoc h3{
  font-weight:bold;
}
.tictactoc .tac_row{
  display:flex;
  margin-bottom:4px;
  gap:4px;
}
.tictactoc .tac_row button{
  width:100px;
  height:100px;
  outline:none;
  border:5px solid #cc7575;
  border-radius:12px;
}
.tictactoc .tac_row button:hover{
  background-color:#cc7575;
}
.tictactoc .again{
  padding:20px 40px;
  outline:none;
  border-radius:200px;
  border:2px solid #e43d61;
}
@media (max-width: 991px) {
  header #navbarScroll{
    display:none;
  }
  .container_left{
    display:none;
  }
  .container_right{
    display:none;
  }
  .container_center{
    width:100%;
  }
  .home h1{
    font-size:24px;
  }
  .home_profile{
    padding:0 20px;
  }
  .home_profile > div{
    padding:50px 0;
  }
  .btn_group1{
    flex-direction:column;
  }
  .btn_group1 button{
    margin-bottom:20px;
  }
  .home_slide .swiper-slide:first-child{
    width:100%;
  }
  .home_slide .swiper-slide:nth-child(2n) {
    width:100%;
  }
  .home_slide .swiper-slide:nth-child(3n) {
    width:100%;
  }
  .home_skill .skill_nav .nav-tabs .nav-item{
    flex-basis:50%;
  }
  .shop_title{
    flex-direction:column;
    align-items:flex-start;
  }
  .mall_list .itemcard_wrap{
   display:flex;
  }
  .mall_list .itemcard_wrap .item{
    display:flex;
    flex-direction:column;
    flex-basis:33.3%;
   }
  .mall_list .item p{
    font-size:12px;
  }
  .item_tag{
    display:block;
    text-align:center;
  }
  .mall_detail{
    flex-direction:column;
  }
  .mall_detail .thumb_info{
    margin:0 0 20px 0;
    width:100%;
  }
  .mall_detail .thumb_summary ul li{
    flex-direction:column;
    text-align:left;
  }
  .mall_detail .thumb_summary ul li span, .mall_detail .thumb_summary ul li strong{
    width:100%;
    text-align:left;
  }
  .cart_list .cart_myselect{
    font-size:12px;
  }
  .myPortfolio .portfolio_list{
    width:100%;
    margin:0 auto;
  }
  .portfolio_gallery{
    flex-direction:column;
    gap:0;
  }
  .portfolio_gallery > div{
    margin-bottom:20px;
  }
  .portfolio_gallery > div >div{
    display:flex;
    flex-wrap:wrap;
  }
  .portfolio_gallery span{
    margin-bottom:10px;
    font-size:10px;
  }
  .porfol_detail{
    width:80%;
  }
  .porfol_detail .pd_text > p:first-child{
    flex-direction:column;
    align-items:flex-start;
    font-size:12px;
    line-height:200%;
  }
  .porfol_detail .pd_text h2{
    font-size:22px;
    font-weight:bold;
  }
  .porfol_detail .pd_tag span{
    font-size:12px;
  }
  .porfol_detail .pd_text p{
    font-size:14px;
  }
  .data_title h3{
    font-size:18px;
    font-weight:bold;
  }
  .dog_option{
    flex-direction:column;
  }
  .dog_option select{
    margin-bottom:20px;
    font-size:14px;
  }
  .dog_result .dog_card{
    flex-basis:45%;
  }
  .community_list table tr{
    font-size:14px;
  }
}
@media (max-width: 426px) {
  .home h1{
    font-size:20px;
  }

}
